<template>
  <div class="listCandidates">
    <div class="listCandidates__left">
    </div>
   <div class="listCandidates__main">
     <h1 class="listCandidates__title">Кандидаты
       <span class="listCandidates__subtitle">RITG V</span>
     </h1>

     <form action="#" class="listCandidates__form">
       <p class="form__text">ФИО</p>
       <input type="form__input" class="form__input">
       <p class="form__text form__text-date">Дата добавления</p>
       <input class="form__input form__input-date" type="text">
       <p class="form__text form__text-separator">-</p>
       <input class="form__input form__input-date" type="text">
     </form>

     <div class="listCandidates__table listCandidates__table-title">
       <div class="listCandidates__table-number">№ п/п</div>
       <div class="listCandidates__table-name">ФИО</div>
       <div class="listCandidates__table-date">Дата рождения</div>
       <div class="listCandidates__table-technology">Технологии</div>
       <div class="listCandidates__table-description">Описание</div>
       <div class="listCandidates__table-image"></div>
     </div>

     <div class="listCandidates__data" v-for="(candidate, index) in this.candidates" :key="candidate.id">
       <div class="listCandidates__data-number">{{ index + 1}}</div>
       <div class="listCandidates__data-name">{{candidate.name}}</div>
       <div class="listCandidates__data-date">{{candidate.date}}</div>
       <div class="listCandidates__data-technology">

         <div class="listCandidates__data-php">
           <div class="language-name">PHP</div>
           <div class="language-chart">
             <div class="language-chart__item" :class="(candidate.technology.php == 1 || candidate.technology.php == 2 || candidate.technology.php == 3) ? 'language-chart__item-red' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.php == 2 || candidate.technology.php == 3) ? 'language-chart__item-red' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.php == 3) ? 'language-chart__item-red' : ''"></div>
           </div>
         </div>

         <div class="listCandidates__data-JAVA">
           <div class="language-name">JAVA</div>
           <div class="language-chart">

             <div class="language-chart__item" :class="(candidate.technology.java == 1 || candidate.technology.java == 2 || candidate.technology.java == 3) ? 'language-chart__item-blue' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.java == 2 || candidate.technology.java == 3) ? 'language-chart__item-blue' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.java == 3) ? 'language-chart__item-blue' : ''"></div>
           </div>
         </div>

         <div class="listCandidates__data-PYTHON">
           <div class="language-name">PYTHON</div>
           <div class="language-chart">
             <div class="language-chart__item" :class="(candidate.technology.python == 1 || candidate.technology.python == 2 || candidate.technology.python == 3) ? 'language-chart__item-green' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.python == 2 || candidate.technology.python == 3) ? 'language-chart__item-green' : ''"></div>
             <div class="language-chart__item" :class="(candidate.technology.python == 3) ? 'language-chart__item-green' : ''"></div>
           </div>
         </div>

       </div>
       <div class="listCandidates__data-description">{{candidate.description}}</div>
       <div class="listCandidates__data-image"></div>
     </div>
   </div>
  </div>
</template>

<script>
export default {
  name: 'ListCandidates',
  data(){
    return {
      candidates: null,
  }
  },
  mounted(){
    fetch('http://localhost:3000/candidates')
        .then(response => response.json())
        .catch(() => console.log('no'))
        .then(candidates => {
          this.candidates = candidates
          console.log(this.candidates);
        })
  }
}
</script>

<style scoped>

.listCandidates{
  display: flex;
}
.listCandidates__left{
  flex: 0 0 63px;
  height: auto;
  margin-top: 80px;

  background: url(../assets/imageLeftArticle.png) center top / auto auto space scroll;
}
.listCandidates__main{
  flex: 1 0 0;
  border: 1px solid #000;
}
.listCandidates__title {
  height: 80px;
  line-height: 80px;
  font-size: 22px;
  text-transform: uppercase;
  text-align: center;
  border: 1px solid #000;

  position: relative;
}
.listCandidates__subtitle{
  position: absolute;
  right: 20px;
  top: 0;
  font-weight: 700;
  font-size: 36px;
}

.listCandidates__form{
  padding: 25px;
  display: flex;
  width: 60%;
  justify-content: space-between;

  margin-bottom: 40px;
}
.form__text{
  display: flex;
  text-align: center;
  align-items: center;
}
.form__text-date{
  margin-left: 100px;
}
.form__input{
  border: 1px solid #000;
  border-radius: 5px;
  height: 30px;
  padding: 5px;
}
.form__input-date{
  width: 50px;
}
.listCandidates__table,
.listCandidates__data{
  display: grid;
  grid-template-columns: 40px 2fr 1fr 2fr 3fr 150px;
  grid-template-rows: 1fr;

  grid-gap: 2px;
  background-color: grey;
  padding: 2px;
}
.listCandidates__table{
  padding-bottom: 0;
}
.listCandidates__table div,
.listCandidates__data div{
  background-color: #fff;
  padding: 5px 5px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.listCandidates__data .listCandidates__data-technology{
  display: block;
}

.listCandidates__data-php,
.listCandidates__data-JAVA,
.listCandidates__data-PYTHON{
  display: flex;
}

.language-name{
  flex: 1 0 0;
}
.language-chart{
  flex: 1 0 0;
  padding: 5px 10px;
}
.language-chart__item{
  width: 30px;
  height: 30px;
  border: 1px solid grey;
}
.language-chart__item-red{
  background-color: red !important;
}
.language-chart__item-green{
  background-color: green !important;
}
.language-chart__item-blue{
  background-color: blue !important;
}
</style>
